import React from 'react';
import {NavLink} from 'react-router-dom';


function NavBar() {
    return (
        <>


            <div className="container-fluid nav_bg ">
                <div className="row">
                    <div className="col-10 mx-auto">


            <nav className="navbar navbar-expand-lg navbar-light">
  <div className="container-fluid">
    <NavLink activeClassName='active-nav' className="navbar-brand" exact to="/">MyWeb</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">

      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
      
        <li className="nav-item">
          <NavLink activeClassName='active-nav' className="nav-link active" aria-current="page" exact to="/">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName='active-nav' className="nav-link" exact to="/About">About</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName='active-nav' className="nav-link" exact to="/Services">Services</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName='active-nav' className="nav-link" exact to="/Contact">Contact</NavLink>
        </li>

      </ul>

    </div>
  </div>
</nav>
                    </div>
                </div>
            </div>

        </>
    )
}

export default NavBar
